// Simple AI translation function (you can replace with actual AI service)
const translateWithAI = async (text: string, targetLang: string): Promise<string> => {
  // For demo purposes, return the text with a note
  // In production, integrate with OpenAI, Google Translate API, etc.
  if (targetLang === 'en') {
    // Simple English translations for common Swedish terms
    const translations: { [key: string]: string } = {
      'Avgngar': 'Departures',
      'Ankomster': 'Arrivals',
      'Sk hllplats': 'Search station',
      'Trafikstrningar': 'Traffic disruptions',
      'Inga avgngar hittades': 'No departures found',
      'Allt flyter p': 'Everything is running smoothly',
      'Inga trafikstrningar just nu': 'No traffic disruptions at the moment',
      'VSTTGEN': 'VSTTGEN',
      'TG': 'TRAIN',
      'strningar': 'disruptions',
      'aktiva': 'active',
      'kritiska': 'critical',
      'normala': 'normal',
      'lgre': 'minor',
      'pgende': 'ongoing',
      'planerade': 'planned',
      'Hndelser skapade idag': 'Events created today',
      'gamla hndelser': 'old events',
      'nya hndelser': 'new events'
    };

    // Simple word-by-word translation
    return text.split(' ').map(word => translations[word] || word).join(' ');
  }

  return text; // Return original text for other languages
};

